    .navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #0d47a1;
        color: #fff;
        padding: .8em;
        direction: ltr;
    }
    
    .iconright {
        flex-basis: 60%;
        display: flex;
        justify-content: space-between;
    }
    
    .header {
        display: flex;
        /* justify-content: stretch; */
        page-break-after: auto;
        padding: 0.4em;
        background: #e9ece7;
        color: #968d8e;
        align-items: center;
    }
    
    .ContentFolderAndFile {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .8em .5em;
        background: #fdfdfd;
        font-size: 0.8em;
        box-shadow: 0px 0.5px 3px rgb(0 0 0 / 80%);
        color: #000;
        /* transition: all 0.8s ease;*/
        cursor: pointer;
        /* border-bottom: 0.1px solid #000; */
    }
    
    .ContentFolderAndFile:hover {
        background: #0d47a1;
        color: #fff;
        transition: all .5s ease-in-out;
    }
    
    .ContentFolderAndFile:hover h4 {
        color: #fff;
    }
    
    .h4wrap {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        max-width: 250px;
    }
    
    .ContentFolderAndFile .titleFolder>h4:hover {
        color: #fff;
    }
    
    .active {
        background: #0f4168;
        color: #fff;
    }
    
    .svgicon {
        fill: #0d47a1;
    }
    
    .ContentFolderAndFile:hover>.svgicon {
        fill: #ffd965;
    }
    
    .titleFolder>h4:hover {
        color: #fff;
    }
    
    .titleFolder {
        flex-basis: 80%;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        margin-right: 1em;
        line-height: 0.5;
        /*user-select: none;*/
        /*color: #000;*/
    }
    
    .folderdetails {
        display: flex;
        color: #afafaf;
    }
    
    .folderdetails>span {
        padding: 0 .5em;
    }
    
    .iconfolder {
        flex-basis: 30%;
        position: relative;
    }
    
    .menuiconfolder {
        background: #f1f1f1;
        padding: 1em;
        border-radius: .2em;
        box-shadow: 0px 0.3px 2px rgb(0 0 0 / 80%);
        position: absolute;
        left: 30px;
        top: 0;
        z-index: 12;
        display: none;
    }
    
    .activmen.menuiconfolder {
        display: flex;
        flex-direction: column;
        width: 130px;
        align-items: center;
    }
    
    .menuiconfolder>a {
        background-color: white;
        width: 100%;
        padding: 0.3em 1em;
        text-align: center;
        -webkit-user-select: none;
        user-select: none;
        color: #000;
        border-radius: .5em;
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-decoration: none;
        margin: 2px;
    }
    
    a:not([href]):not([class]) {
        color: #000;
        text-decoration: none;
    }
    
    .menuiconfolder>a:hover {
        background-color: #575757;
        color: #fff;
        cursor: pointer;
    }
    
    .overfloeiconfolder {
        width: 100%;
        height: 100vh;
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        z-index: 10;
        background: #00000017;
    }
    
    .headermenufolder {
        background: #e5e5ea;
        width: 100%;
        padding: .2em;
        text-align: center;
        margin-bottom: 0.5em;
        border-bottom: 1px solid #a7580e;
        color: #000;
    }
    /*Rename form*/
    
    .rename {
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, -50%);
        width: 90%;
        background-color: #f7f7fa;
        /* color: #f1ff; */
        padding: 0 1em 1em 1em;
        border-radius: .5em;
        z-index: 855656;
        box-shadow: 0px 0.3px 2px rgb(0 0 0 / 80%);
    }
    
    .renameheader {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1em;
    }
    
    input.inputrename {
        padding: .5em;
        outline: none;
        width: 100%;
        font-weight: 700;
    }
    
     ::placeholder {
        font-size: 12px;
    }
    
    .btnrename {
        padding: 1em;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        font-weight: 700;
    }
    
    .btnrename>button {
        padding: .5em 2em;
        margin-top: .5em;
        border: none;
        outline: none;
        overflow: hidden;
        transition: all .5s ease-in-out;
    }
    
    .btnrename>button:nth-child(1) {
        background-color: #0f345b;
        color: #fff;
    }
    
    .btnrename>button:nth-child(2) {
        background-color: #88592d;
        color: #fff;
    }
    
    .fileuploaddials {
        padding: 5px;
        background: #ecf6ff;
        color: #000;
        display: flex;
        align-items: flex-start;
        margin: 5px;
        font-size: .8em;
        width: 95%;
        min-width: 95%;
        min-height: 30px;
        word-break: break-all;
        border-bottom: 1px solid;
    }
    
    .formupload {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
    }
    
    .selectfile {
        background: rgb(240 247 255 / 50%);
        width: 90%;
        height: 90%;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #000;
        border: 1px solid rgb(28, 28, 94);
        flex-direction: column;
        cursor: pointer;
    }
    
    .headeruploadfile {
        width: 100%;
        background: #585858;
        color: #fff;
        text-align: center;
        padding: 10px;
    }
    
    .menuiconfolder>p {
        margin-top: 0;
        margin-bottom: .3em;
    }
    
    .resultuploadfile {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .ant-popconfirm {
        z-index: 10645440;
    }
    
    .SlideUsers {
        background: #0c4193;
        width: 200px;
        height: 100vh;
        position: fixed;
        top: 0;
        right: 0;
        padding: 1em;
        color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        transition: all .5s ease-in-out;
        z-index: 454545;
        transform: translateX(0%);
    }
    
    .SlideUsershidde {
        background: #0c4193;
        width: 200px;
        height: 100vh;
        position: fixed;
        top: 0;
        right: 0;
        padding: 1em;
        color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        transition: all .5s ease-in-out;
        z-index: 454545;
        transform: translateX(110%);
    }
    
    .SlideUsers>h5 {
        color: #fff;
        margin: 5px;
    }
    
    .slidbtn {
        background: #e5e6e4;
        padding: 0.2em;
        width: 100%;
        text-align: center;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #000;
        margin: .2em 0;
        border-radius: 5px;
    }
    
    .slidbtn>h5 {
        color: #000;
        margin: 5px;
    }
    /*iploade file*/
    
    .mainupload {
        display: flex;
        flex-direction: column;
        height: 100vh;
        width: 100%;
    }
    
    .headeruploadefile {
        background: #f5f5f5;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .6em 1em;
        flex-basis: 5%;
    }
    
    .bodyuploadefile {
        position: relative;
        /* flex-direction: column; */
        /* justify-content: center; */
        align-items: center;
        flex-basis: 80%;
        overflow: auto;
        background: #ffffff;
        place-items: center;
        /* align-items: center; */
        /* text-align: center; */
        display: grid;
    }
    
    .footeruploadefile {
        background: #f5f5f5;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .6em 1em;
        flex-basis: 5%;
        position: fixed;
        bottom: 0;
        width: 100%;
    }
    
    .detfiledata {
        width: 100%;
        height: 90%;
        position: relative;
        /* height: 100vh; */
        /* flex-direction: column; */
        /* justify-content: center; */
        align-items: center;
        flex-basis: 90%;
        overflow: auto;
        background: #ffffff;
        place-items: center;
        padding-bottom: 10px;
    }
    
    .btnuploadfile {
        padding: .5em;
        background: #1890ff;
        color: #fff;
        border-radius: 5px
    }
    
    .btnuploadfile:hover {
        background: red;
    }
    
    .closeupload {
        background: #dc3545;
        padding: 0 1em;
        font-weight: 700;
        color: #fff;
        border-radius: 5px;
    }
    
    button[disabled],
    html input[disabled] {
        cursor: default;
        background: gray;
        opacity: .1;
    }
    
    .detailsfile {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0 5px;
        transition: all .3s ease-in-out;
    }
    /*serach input*/
    
    .closeserch {
        border-radius: 5px;
        flex-basis: 8%;
        background: #dc3545;
        padding: 5px 10px;
        transition: all .3s ease-in-out;
    }
    
    .formserch {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        transition: all .3s ease-in-out;
    }