.users_header{
   min-height: 60px;
    background-color: #0c1c71;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    padding: 0 20px;
}

.add_users{
   background-color:#0b0e2b;
    padding: 5px;
    color: white;
    cursor: pointer;
    margin: 0px;
    border-radius: 5px;
    border: 1px solid;
    transition: all 0.3s ease-in-out;
    display: flex;
    justify-content: center;
}

.delall_users{
     background-color:#6D4C41;
    padding: 5px;
    color: white;
    cursor: pointer;
    margin: 0px;
    border-radius: 5px;
    border: 1px solid;
    transition: all 0.3s ease-in-out;
    display: flex;
    justify-content: center;
}

.add_users:hover,.delall_users:hover{
     background-color:#F44336;
}
.input_seache_users{
   border-radius: 2px;
    outline: navajowhite;
    padding: 2px 5px;
    margin-right: 10px;
    width: 260px;
    border: 0;
   color:black;
    padding-left: 40px;
   
}

.box_serach{
    position: absolute;
    top: 50%;
    left: 0;
    /* z-index: 1255; */
    background: #FF5722;
    transform: translateY(-50%);
    color: #ffffff;
    height: 100%;
    width: 35px;
    cursor: pointer;
    

}

/*add folder */
.divaddfolder{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px 0;
    flex-wrap: wrap;
}

.deletefolder{
    position: absolute;
    left: 0;
    top: 0;
    background: #fb1100;
    width: 20px;
    height: 25px;
    color: #fff;
}
/* Add user Form*/
.addusernew{

    padding: 30px;
}


/* Files user Form*/
.folder_files_users{
background: #efefef;
   
    display: flex;
    flex-direction: column;
    text-align: right;
    padding: 8px 20px;
    border-bottom: 3px solid #607D8B;

}

.folderfiles_header{
    border-bottom: 2px solid #795548;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.folderfiles_content{
    display: flex;
   
    align-items: center;
    height: 100%;
    padding: 5px 0;
}


.box_folderuser{
    background-color:#FFC107;
    height: 100%;
    width: 100px;
    cursor: pointer;
    color: black;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    border: 1px solid #868181;
    margin: 5px;
    transition: all 0.3s ease-in-out;
    position: relative;
}

.box_folderuser:hover{
 background-color: #430c8b;
 color: #fff;
}

.files_users{
    background: #efefef;
    display: flex;
    flex-direction: column;
    text-align: right;
    padding: 8px 20px;
    border-bottom: 3px solid #607D8B;
}

.box_filesuser{
       background-color: #4d4f54;
    max-height: 280px;
    min-height: 280px;
    max-width: 200px;
cursor: pointer;
    color: white;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    border-radius: 2px;
    border: 1px solid #868181;
    flex-direction: column;
    margin-left: 5px;
    margin-top: 10px;
     transition: all 0.3s ease-in-out;
}

.box_filesuser:hover{
 background-color: #430c8b;
 color: #fff;
}

.img_files{
    width: 200px;
    height: 150px;
}

.files_content {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 5px 0;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.upload-btn-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.btnc {
  border: 2px solid gray;
  color: gray;
  background-color: white;
  padding: 8px 20px;
  border-radius: 8px;
  font-size: 20px;
  font-weight: bold;
}

.upload-btn-wrapper input[type=file] {
  font-size: 100px;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}


.divimgshow{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 50%;
    height: 90vh;
    z-index: 54887;
    background: #a7a7a7;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border: 1px solid black;
    overflow: auto;
}

.closedivimg{
    position: absolute;
    background: red;
    top: -20px;
    left: 0;
    width: 20px;
    text-align: center;
    cursor: pointer;
    color: #fff;
    background-color: rgba(red, green, blue, alpha);
}

.overflowimg{
        width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 1);
    z-index: 454447;
}

.overflowimgmus{
        width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 100;
}

.folderlengh{
       position: absolute;
    top: -15px;
    left: -10px;
    background: #F44336;
    border-radius: 50%;
    padding: 5px;
    color: #fff;
}

.mune_mobail{
        width: 100%;
    padding: 20px;
    background: #c7c7c7;
    color: #fff;
    font-weight: 700;
    transition: all .3s ease-in-out;
}

.alert_confirm{
    width: 600px;
    height: 200px;
    background: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    flex-direction: column;
    box-shadow: 0 4px 4px rgba(0,0,0,.12), 0 0 10px rgba(0,0,0,.06);
    font-weight: 700;
    z-index: 4454545;
}
.m_mobail{
    display: none;
}

