.printkshfuser {
    background: #0d47a1;
    color: #fff;
    text-align: center;
    padding: 10px;
    font-weight: 800;
    /* margin-top: 20px; */
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    border-top: 2px solid #fff;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}

.printkshfuser:hover {
    background: rgb(216, 190, 74);
    color: #000000;
    border-top: 2px solid #000;
}

